  import urls from '../../../utils/urls.js'
  export default {
    data () {
      return {
        user: {},
        comment: [],
        bookshelf: {},
        focusTxt: '关注',
        animTitle: {},
				isLogin: this.$store.state.isLogin,
      }
    },
    onLoad() {
      let fuid = this.$store.state.otherUser.id
      let uid = 0;
			if(this.isLogin==true){
      	 uid = this.$store.state.userInfo.id
      }
			uni.request({
        url: urls.HTTP + '/index/getuserinfo',
        method: 'POST',
        data: {
          fuid: fuid,
          uid: uid
        },
        success: (res) => {
          // console.log(res)
          this.user = res.data.user
          this.comment = res.data.comment
          this.bookshelf = res.data.bookshelf
          this.focusTxt = res.data.tip
        }
      })
    },
    onShow() {
      var animation = uni.createAnimation({
        duration: 1000
      })
      this.animation = animation
    },
    onPageScroll (res) {
      if (res.scrollTop > 20) {
        this.animation.opacity(1).step()
        this.animTitle = this.animation.export()
      } else if (res.scrollTop < 20) {
        this.animation.opacity(0).step()
        this.animTitle = this.animation.export()
      }
    },
    methods: {
      imgUrl: function (img) {
        return urls.UrlHttp + img
      },
      goDetail: function (id) {
        uni.navigateTo({
          url: 'storyDetail?id=' + id
        })
      },
      focusTa: function () {
			if(!this.isLogin){
				 uni.showModal({
				  title: '提示',
				  content: '您还没有登录，是否登录？',
				  success: (res) => {
				    if (res.confirm) {
				      uni.navigateTo({
				        url: '../login'
				      })
				    }
				  }
				})
				return false
			}
        let fuid = this.otherUser.id
				
        let uid = this.userInfo.id
				
        if (fuid !== uid) {
          uni.request({
            url: urls.HTTP + '/user/follow',
            method: 'POST',
            data: {
              fuid: fuid,
              uid: uid
            },
            success: (res) => {
              // console.log(res)
              if (res.data.code === 0) {
                this.focusTxt = res.data.tip
                uni.showToast({
                  title: res.data.error
                })
              } else {
                uni.showToast({
                  title: res.data.error,
                  icon: 'none'
                })
              }
            }
          })
        } else {
          uni.showToast({
            title: '自己不能关注自己哦',
            icon: 'none'
          })
        }
      }
    },
    computed: {
      userInfo () {
        return this.$store.state.userInfo
      },
      otherUser () {
        return this.$store.state.otherUser
      },
      isFocus () {
        if (this.focusTxt === '已关注') {
          return true
        } else {
          return false
        }
      }
    }
  }
